import React, {useState, useEffect} from 'react';
import { Link } from 'react-router-dom';
import "./Navbar.css";
import { Button } from './Button';

function Navbar() {
    const [click, setClick] = useState(false);
    const [button, setButton] = useState(true);

    const handleClick = () => setClick(!click);
    const closeMobileMenu = () => setClick(false);

    const showButton = () => {
        if(window.innerWidth <= 960) {
            setButton(false);
        } else {
            setButton(true);
        }
    };

    useEffect(() => {
        showButton();
    }, []); 

    window.addEventListener("resize", showButton);

    return (
      <>
        <nav className="navbar">
            <div className="navbar-container">
                <Link to="/" className="navbar-logo" onClick={closeMobileMenu}>
                    WELLNESS4ALL <i class="fas fa-spa"></i>
                </Link>
                <div className="menu-icon" onClick={handleClick}>
                    <i className={click ? "fas fa-times" : "fas fa-bars"} />
                </div>
                <ul className={click ? "nav-menu active" : "nav-menu"}>
                    <li className="nav-item">
                        <Link to="/" className="nav-links" onClick={closeMobileMenu}>
                            Home
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link to="/yoga" className="nav-links" onClick={closeMobileMenu}>
                            Yoga
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link to="/nutrition" className="nav-links" onClick={closeMobileMenu}>
                            Nutrition
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link to="/plant-blog" className="nav-links" onClick={closeMobileMenu}>
                            Plant Blog
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link to="/sign-up" className="nav-links-mobile" onClick={closeMobileMenu}>
                            Sign Up
                        </Link>
                    </li>
                </ul>
                {button &&  <Button buttonStyle="btn--outline">SIGN UP</Button>}
            </div>
        </nav>
      </>
    )
}

export default Navbar;